import React from 'react'
import { graphql } from 'gatsby'

export default ({ data }) => {
  console.log(data)
  const { edges } = data.allFormFieldsJson
  return (
    <>
      {edges.map(({ node: { content } }) => (
        <p>{content}</p>
      ))}
    </>
  )
}

export const query = graphql`
  query {
    allFormFieldsJson {
      edges {
        node {
          content
        }
      }
    }
  }
`
